import { useState, useEffect } from "react";
import { Container, Row, Col } from "react-bootstrap";
import { ArrowRightCircle } from 'react-bootstrap-icons';
import 'animate.css';
import { Particles1 } from "./Particles";
import ProfilePicture from "../assets/img/ProfilePicture.png";

export const Banner = () => {

  return (
    <section className="banner" id="home">
      <Particles1/>
      <Container>
        <Row className="aligh-items-center">
          <Col xs={12} md={6} xl={7}>
            <div className="animate__animated animate__fadeIn">
              <h1>{`Hello! I'm Aadil Umar`}</h1>
                <p>I am an undergraduate computer science student at the University of California, Riverside. I love working on interesting projects and ideas,
                    with experience including creating games, websites, and machine learning applications.
                </p>
                <button onClick={() => console.log('connect')}>Let’s Connect <ArrowRightCircle size={25} /></button>
            </div>
          </Col>
          <Col xs={12} md={6} xl={5}>
            <div className= "animate__animated animate__zoomIn">
              <img src={ProfilePicture} alt="Header Img"/>
            </div>
          </Col>
        </Row>
      </Container>
    </section>
  )
}
