import meter1 from "../assets/img/meter1.svg";
import meter2 from "../assets/img/meter2.svg";
import meter3 from "../assets/img/meter3.svg";
import Carousel from 'react-multi-carousel';
import 'react-multi-carousel/lib/styles.css';
import arrow1 from "../assets/img/arrow1.svg";
import arrow2 from "../assets/img/arrow2.svg";
import colorSharp from "../assets/img/color-sharp.png";
import CPPLogo from "../assets/img/c++.png";
import CSharpLogo from "../assets/img/csharp.png"; 
import PythonLogo from "../assets/img/python.png";
import GithubLogo from "../assets/img/github.png";
import ReactLogo from "../assets/img/reactjs.png";
import MySQLLogo from "../assets/img/mysql.png";
import JavaLogo from "../assets/img/java.png";

export const Skills = () => {
  const responsive = {
    superLargeDesktop: {
      // the naming can be any, depends on you.
      breakpoint: { max: 4000, min: 3000 },
      items: 5
    },
    desktop: {
      breakpoint: { max: 3000, min: 1024 },
      items: 3
    },
    tablet: {
      breakpoint: { max: 1024, min: 464 },
      items: 2
    },
    mobile: {
      breakpoint: { max: 464, min: 0 },
      items: 1
    }
  };

  return (
    <section className="skill" id="skills">
        <div className="container">
            <div className="row">
                <div className="col-12">
                    <div className="skill-bx wow zoomIn">
                        <h2>Skills</h2>
                        <p>Throughout my classes and projects, I've gained experience with a number of languages and technologies.</p>
                        <Carousel responsive={responsive} infinite={false} className="owl-carousel owl-theme skill-slider">
                            <div className="item">
                                <img className="skill-image" src={CPPLogo} alt="Image" />
                                <h5>C++</h5>
                            </div>
                            <div className="item">
                                <img src={CSharpLogo} alt="Image" />
                                <h5>C#</h5>
                            </div>
                            <div className="item">
                                <img src={PythonLogo} alt="Image" />
                                <h5>Python</h5>
                            </div>
                            <div className="item">
                                <img src={GithubLogo} alt="Image" />
                                <h5>Github</h5>
                            </div>
                            <div className="item">
                                <img src={ReactLogo} alt="Image" />
                                <h5>React</h5>
                            </div>
                            <div className="item">
                                <img src={MySQLLogo} alt="Image" />
                                <h5>MySQL</h5>
                            </div>
                            <div className="item">
                                <img src={JavaLogo} alt="Image" />
                                <h5>Java</h5>
                            </div>
                        </Carousel>
                    </div>
                </div>
            </div>
        </div>
        <img className="background-image-left" src={colorSharp} alt="Image" />
    </section>
  )
}
