import { Container, Row, Col, Tab, Nav } from "react-bootstrap";
import { ProjectCard } from "./ProjectCard";
import projImg1 from "../assets/img/project-img1.png";
import projImg2 from "../assets/img/project-img2.png";
import projImg3 from "../assets/img/project-img3.png";
import colorSharp2 from "../assets/img/color-sharp2.png";
import ChessGameImage from "../assets/img/ChessGame.png";
import BitplaneImage from "../assets/img/Bitplane.png";
import OpenCVImage from "../assets/img/OpenCVImage.png";
import MoviesImage from "../assets/img/MoviesPosters.png";
import RCycleImage from "../assets/img/RCycle.png";
import ACMImage from "../assets/img/ACM.png";
import 'animate.css';
import TrackVisibility from 'react-on-screen';

export const Projects = () => {

  const projects = [
    {
      title: "Chess Game",
      description: "A fully functional Chess game developed using C++ and the SDL library for rendering." + 
      " This game was created in collaboration with two other students and employed collaborative tools and practices to ensure a" + 
      " polished project.",
      imgUrl: ChessGameImage,
      linkUrl: "https://github.com/AadilU/ChessGameCS100",
    },
    {
      title: "OpenCV Facial Recognition",
      description: "An application which recognizes someone's face and finds their celebrity lookalike from a set of celebrity images." +
      " This project uses python and the OpenCV library, an open source computer vision library, for facial recognition and uses machine learning" +
      " to match the person's face with a celebrity.",
      imgUrl: OpenCVImage,
      linkUrl: "https://github.com/skyguy-netizen/Cutie-Hack-2022",
    },
    {
      title: "Bitplane",
      description: "A mobile arcade game made with Unity. This game was developed entirely by me using the Unity game engine and published to " +
      "the Google Play Store.",
      imgUrl: BitplaneImage,
      linkUrl: "https://play.google.com/store/apps/details?id=com.PickleStreetGames.Bitplane&hl=en_US&gl=US",
    },
    {
      title: "Movie Search Engine",
      description: "A Java app which allows users to search which platforms a movie is available on. This project uses the Utelly API to " + 
      "query data on movies given a user's search.",
      imgUrl: MoviesImage,
      linkUrl: "https://github.com/AadilU/CutieHack2021",
    },
    {
      title: "UCR ACM Membership Portal",
      description: "A website which allows UCR students to access professional resources provided by ACM. This project was made " + 
      "by a team of numerous developers who utilized React JS, Next JS, Firebase, and Tailwind CSS.",
      imgUrl: ACMImage,
      linkUrl: "https://github.com/acm-ucr/membership-portal",
    },
    {
      title: "R'Cycle Co-op Website",
      description: "A website which provides information and links for a cycling organization on campus. This website was " +
      "developed using pure HTML, CSS, and JavaScript.",
      imgUrl: RCycleImage,
      linkUrl: "https://github.com/AadilU/RCycleWebsite",
    },
  ];

  return (
    <section className="project" id="project">
      <Container>
        <Row>
          <Col size={12}>
            <TrackVisibility>
              {({ isVisible }) =>
              <div className={isVisible ? "animate__animated animate__fadeIn": ""}>
                <h2>Projects</h2>
                <p>Some of the projects I've worked on both by myself and with others.</p>
                <Tab.Container id="projects-tabs" defaultActiveKey="first">
                  {/* <Nav variant="pills" className="nav-pills mb-5 justify-content-center align-items-center" id="pills-tab">
                    <Nav.Item>
                      <Nav.Link eventKey="first">Tab 1</Nav.Link>
                    </Nav.Item>
                    <Nav.Item>
                      <Nav.Link eventKey="second">Tab 2</Nav.Link>
                    </Nav.Item>
                    <Nav.Item>
                      <Nav.Link eventKey="third">Tab 3</Nav.Link>
                    </Nav.Item>
                  </Nav> */}
                  <Tab.Content id="slideInUp" className={isVisible ? "animate__animated animate__slideInUp" : ""}>
                    <Tab.Pane eventKey="first">
                      <Row>
                        {
                          projects.map((project, index) => {
                            return (
                              <ProjectCard
                                onClick={() => console.log("test")}
                                key={index}
                                {...project}
                                />
                            )
                          })
                        }
                      </Row>
                    </Tab.Pane>
                    <Tab.Pane eventKey="section">
                      <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Cumque quam, quod neque provident velit, rem explicabo excepturi id illo molestiae blanditiis, eligendi dicta officiis asperiores delectus quasi inventore debitis quo.</p>
                    </Tab.Pane>
                    <Tab.Pane eventKey="third">
                      <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Cumque quam, quod neque provident velit, rem explicabo excepturi id illo molestiae blanditiis, eligendi dicta officiis asperiores delectus quasi inventore debitis quo.</p>
                    </Tab.Pane>
                  </Tab.Content>
                </Tab.Container>
              </div>}
            </TrackVisibility>
          </Col>
        </Row>
      </Container>
      <img className="background-image-right" src={colorSharp2}></img>
    </section>
  )
}
